// css button, title, icon, elements...
img {
    max-width: 100%;
    display: block;
}
// strong {
//     font-family: ib;
// }
a {
    font-size: $fs-body;
    text-decoration: none;
    color: $white-cl;
    outline: 0;
}

////////////////////// HEADING ///////////////////////

.heading {
    font-family: c;
    &.--h1 {
        font-size: $fs-h1;
        line-height: $lh-h1;
    }
    &.--h2 {
        font-size: $fs-h2;
        line-height: $lh-h2;
    }
    &.--h3 {
        font-size: $fs-h3;
        line-height: $lh-h3;
    }
    &.--h4 {
        font-size: $fs-h4;
        line-height: $lh-h4;
    }
    &.--h5 {
        font-family: cm;
        font-size: $fs-h5;
        line-height: $lh-h5;
    }
    &.--white {
        color: $white-cl;
    }
    &.--black {
        color: $black-cl;
    }
