}

////////////////////// BODY TEXT ///////////////////////
.--text {
    font-family: i;
    font-size: $fs-body;
    line-height: $lh-body;
    &.bold {
        font-family: is;
    }
}
.--text-s {
    font-family: i;
    font-size: $fs-body-s;
    line-height: $lh-body-s;
    &.bold {
        font-family: i;
    }
}
.--text-xs {
    font-family: i;
    font-size: $fs-body-xs;
    line-height: $lh-body-xs;
    &.bold {
        font-family: is;
    }
}

