.nav {
    display: none;
    
    @include sm {
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 29px;
        padding-bottom: 29px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        @include size (100vw,auto);
        &__logo {
            @include size(auto, 20px);
        }
        &__menu {
            @include mid(true);
            .logo_lang {
                margin-right: 22px;
                a {
                    @include text(1.2rem,is,1.8rem,$white30-cl,uppercase);
                }    
            }
            .nav__toggle {
                .button {
                    padding: 0;
                    background-color: $background-cl;
                    span {
                        padding: 11px 12px;
                    }
                    .ic-block {
                        padding: 14px 12px 14px 12px;
                        border-left: solid 1px $gold-cl;
                        img {
                            margin: 0;
                        }
                    }
                }
            }
        }
    }
}