// css components
.button {
    display: flex;
    align-items: center;
    @include text(1.2rem,is,1.8rem,$gold-cl,uppercase);
    letter-spacing: .1em;
    padding: 10px 18px 10px 20px;
    border: solid 1px $gold70-cl;
    img {
        float: left;
        margin-right: 8px;
    }
    &.--big {
        padding: 18px 14px 18px 20px;
    }
}