// css header
header {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 800;
    .container {
        padding-top: 48px;
        padding-bottom: 48px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .logo {
            display: flex;
            align-items: center;
            &_lang {
                margin-left: 12px;
                a {
                    @include text(1.2rem,is,1.8rem,$white30-cl,uppercase);
                }
            }
        }
        .menu {
            display: flex;
            align-items: center;
            li {
                a {
                    margin: 0 28px;
                    letter-spacing: .1em;
                    font-family: is;
                    opacity: .3;
                    @include text(1.2rem,is,1.8rem,$white-cl,uppercase);
                    @include transition($t);
                    &:hover,
                    &.active {
                        opacity: 1;
                        @include transition($t);
                    }
                }
            }
            span {
                width: 1px;
                height: 12px;
                background-color: $white20-cl;
            }
        }
    }

    @include sm {
        display: none;
    }
}